/* ProjectDetails.css - Basit Grid Kartları */

/* Ana scroll bölümü */
.project-details-section {
  background: linear-gradient(135deg, #1e293b 0%, #0f172a 50%, #1e293b 100%);
  padding: 80px 20px;
  min-height: 100vh;
}

.details-container {
  max-width: 1400px;
  margin: 0 auto;
}

/* Başlık bölümü */
.details-header {
  text-align: center;
  margin-bottom: 60px;
}

.details-title {
  font-size: 2.5rem;
  font-weight: bold;
  color: white;
  margin-bottom: 16px;
  background: linear-gradient(135deg, #60a5fa, #a78bfa);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.details-subtitle {
  font-size: 1.1rem;
  color: #cbd5e1;
  max-width: 500px;
  margin: 0 auto;
}

/* Proje kartları grid */
.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 40px;
  margin-top: 40px;
}

/* Her proje kartı */
.project-detail-card {
  background: rgba(30, 41, 59, 0.8);
  backdrop-filter: blur(16px);
  border-radius: 20px;
  padding: 30px;
  border: 1px solid rgba(51, 65, 85, 0.5);
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.project-detail-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.3);
  border-color: rgba(96, 165, 250, 0.6);
}

/* Proje görseli */
.project-detail-image {
  margin-bottom: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Mobil telefon çerçevesi - küçük versiyon */
.detail-mobile-frame {
  width: 140px;
  height: 250px;
  background: linear-gradient(145deg, #2a2a2a, #1a1a1a);
  border-radius: 20px;
  padding: 8px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.4);
  position: relative;
}

.detail-mobile-frame::before {
  content: '';
  position: absolute;
  top: 15px;
  left: 50%;
  transform: translateX(-50%);
  width: 35px;
  height: 3px;
  background: #444;
  border-radius: 2px;
}

.detail-mobile-screen {
  width: 100%;
  height: 200px;
  background: #000;
  border-radius: 12px;
  overflow: hidden;
  margin-top: 20px;
}

.detail-mobile-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 12px;
}

.detail-mobile-home {
  width: 30px;
  height: 30px;
  background: linear-gradient(145deg, #333, #222);
  border-radius: 50%;
  margin: 10px auto 0;
  box-shadow: inset 0 2px 4px rgba(0, 0, 0, 0.2);
}

/* Normal proje görseli */
.project-detail-img {
  width: 280px;
  height: 180px;
  object-fit: cover;
  border-radius: 12px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
}

/* Proje bilgileri */
.project-detail-info {
  color: white;
  width: 100%;
}

/* Proje türü badge */
.project-type-badge {
  display: inline-block;
  background: rgba(96, 165, 250, 0.2);
  color: #60a5fa;
  padding: 6px 14px;
  border-radius: 16px;
  font-size: 0.85rem;
  font-weight: 500;
  margin-bottom: 16px;
  border: 1px solid rgba(96, 165, 250, 0.3);
}

/* Proje başlığı */
.project-detail-title {
  font-size: 1.5rem;
  font-weight: bold;
  color: white;
  margin-bottom: 12px;
}

/* Proje açıklaması */
.project-detail-description {
  font-size: 0.95rem;
  color: #cbd5e1;
  line-height: 1.5;
  margin-bottom: 20px;
}

/* Teknoloji stack */
.tech-stack {
  margin-bottom: 20px;
}

.tech-title {
  font-size: 0.9rem;
  color: #e2e8f0;
  margin-bottom: 10px;
  font-weight: 600;
}

.tech-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
  justify-content: center;
}

.tech-tag {
  background: rgba(167, 139, 250, 0.2);
  color: #a78bfa;
  padding: 4px 10px;
  border-radius: 12px;
  font-size: 0.8rem;
  border: 1px solid rgba(167, 139, 250, 0.3);
}

/* Proje linkleri */
.project-detail-links {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
  margin-bottom: 20px;
}

.detail-link {
  display: inline-flex;
  align-items: center;
  padding: 8px 16px;
  border-radius: 8px;
  text-decoration: none;
  font-weight: 500;
  font-size: 0.85rem;
  transition: all 0.3s ease;
  border: 1px solid transparent;
}

.detail-link.ios-link {
  background: rgba(59, 130, 246, 0.2);
  color: #60a5fa;
  border-color: rgba(59, 130, 246, 0.3);
}

.detail-link.android-link {
  background: rgba(16, 185, 129, 0.2);
  color: #10b981;
  border-color: rgba(16, 185, 129, 0.3);
}

.detail-link.demo-link {
  background: rgba(139, 92, 246, 0.2);
  color: #8b5cf6;
  border-color: rgba(139, 92, 246, 0.3);
}

.detail-link.github-link {
  background: rgba(107, 114, 128, 0.2);
  color: #9ca3af;
  border-color: rgba(107, 114, 128, 0.3);
}

.detail-link:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

/* Özellikler */
.project-features {
  background: rgba(15, 23, 42, 0.5);
  padding: 16px;
  border-radius: 10px;
  border: 1px solid rgba(51, 65, 85, 0.5);
  text-align: left;
}

.features-title {
  font-size: 0.9rem;
  color: #e2e8f0;
  margin-bottom: 10px;
  font-weight: 600;
  text-align: center;
}

.features-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.feature-item {
  color: #cbd5e1;
  margin-bottom: 6px;
  font-size: 0.85rem;
}

/* Responsive tasarım */
@media (max-width: 1024px) {
  .projects-grid {
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    gap: 30px;
  }
  
  .details-title {
    font-size: 2rem;
  }
}

@media (max-width: 768px) {
  .project-details-section {
    padding: 60px 15px;
  }
  
  .projects-grid {
    grid-template-columns: 1fr;
    gap: 25px;
  }
  
  .project-detail-card {
    padding: 20px;
  }
  
  .detail-mobile-frame {
    width: 120px;
    height: 210px;
  }
  
  .detail-mobile-screen {
    height: 160px;
  }
  
  .project-detail-img {
    width: 240px;
    height: 150px;
  }
  
  .details-title {
    font-size: 1.8rem;
  }
}

@media (max-width: 480px) {
  .project-detail-links {
    flex-direction: column;
    align-items: center;
  }
  
  .detail-link {
    width: 180px;
    justify-content: center;
  }
}