/* Ana container */
.app-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #0f172a 0%, #1e293b 50%, #0f172a 100%);
  display: flex;
  margin: 0;
  font-family: Arial, sans-serif;
  /* ÖNEMLİ: Container'ın kendisi scroll olmasın */
  overflow: visible;
}

/* Ana içerik alanı */
.main-content {
  flex: 1;
  padding: 32px;
  background-color: rgba(15, 23, 42, 0.8);
  min-width: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Proje container */
.project-container {
  max-width: 800px;
  width: 100%;
  position: relative;
}

/* Proje kartı */
.project-card {
  background-color: rgba(30, 41, 59, 0.9);
  border-radius: 16px;
  padding: 40px;
  border: 1px solid #334155;
  text-align: center;
  min-height: 500px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

/* Proje görseli */
.project-image {
  width: 100%;
  max-width: 300px;
  height: 300px; /* Mobil telefon çerçevesi için yükseklik artırıldı */
  border-radius: 12px;
  margin: 0 auto 24px auto;
  position: relative;
  overflow: visible; /* Telefon çerçevesinin gölgesi için */
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.project-img {
  width: 100%;
  height: 200px; /* Normal projeler için sabit yükseklik */
  border-radius: 12px;
  object-fit: cover;
  transition: opacity 0.3s ease;
}

/* Mobil telefon çerçevesi */
.mobile-frame {
  width: 160px;
  height: 280px;
  background: linear-gradient(145deg, #2a2a2a, #1a1a1a);
  border-radius: 20px;
  padding: 8px;
  box-shadow: 
    0 0 0 2px #333,
    0 8px 20px rgba(0, 0, 0, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
  position: relative;
  margin: 0 auto;
}

.mobile-frame::before {
  content: '';
  position: absolute;
  top: 15px;
  left: 50%;
  transform: translateX(-50%);
  width: 40px;
  height: 4px;
  background: #444;
  border-radius: 2px;
}

.mobile-frame::after {
  content: '';
  position: absolute;
  top: 25px;
  left: 50%;
  transform: translateX(-50%);
  width: 8px;
  height: 8px;
  background: #333;
  border-radius: 50%;
}

.mobile-screen {
  width: 100%;
  height: 220px;
  background: #000;
  border-radius: 12px;
  overflow: hidden;
  margin-top: 25px;
  position: relative;
}

.mobile-game-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 12px;
}

.mobile-placeholder {
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, #2a4365, #3182ce);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 10px;
  text-align: center;
  padding: 8px;
  border-radius: 12px;
}

.mobile-home-button {
  width: 35px;
  height: 35px;
  background: linear-gradient(145deg, #333, #222);
  border-radius: 50%;
  margin: 8px auto 0;
  box-shadow: 
    inset 0 2px 4px rgba(0, 0, 0, 0.2),
    0 1px 2px rgba(255, 255, 255, 0.1);
}

.image-placeholder {
  width: 100%;
  height: 100%;
  background-color: #475569;
  border-radius: 12px;
  display: none;
  align-items: center;
  justify-content: center;
  color: #cbd5e1;
  font-size: 14px;
  position: absolute;
  top: 0;
  left: 0;
}

/* Proje başlığı */
.project-title {
  font-size: 32px;
  font-weight: bold;
  color: white;
  margin-bottom: 16px;
}

/* Proje açıklaması */
.project-description {
  font-size: 16px;
  color: #cbd5e1;
  margin-bottom: 32px;
  line-height: 1.6;
}

/* Buton container */
.button-container {
  display: flex;
  gap: 16px;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 24px;
}

/* Link butonları */
.link-button {
  color: white;
  padding: 12px 24px;
  border-radius: 8px;
  text-decoration: none;
  font-size: 14px;
  font-weight: 500;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 8px;
}

.link-button:hover {
  transform: translateY(-2px);
}

.ios-button {
  background-color: #3b82f6;
}

.ios-button:hover {
  background-color: #2563eb;
}

.android-button {
  background-color: #10b981;
}

.android-button:hover {
  background-color: #059669;
}

.demo-button {
  background-color: #3b82f6;
}

.demo-button:hover {
  background-color: #2563eb;
}

.github-button {
  background-color: #374151;
}

.github-button:hover {
  background-color: #1f2937;
}

/* Navigasyon butonları */
.nav-button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: rgba(30, 41, 59, 0.9);
  border: 1px solid #334155;
  border-radius: 50%;
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
}

.nav-button:hover {
  background-color: rgba(51, 65, 85, 0.9);
}

.prev-button {
  left: -24px;
}

.next-button {
  right: -24px;
}

/* Göstergeler */
.indicator-container {
  display: flex;
  justify-content: center;
  gap: 8px;
  margin-top: 24px;
}

.indicator-dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.3s ease;
  background-color: #475569;
  position: relative;
}

.indicator-dot:hover {
  transform: scale(1.2);
}

.indicator-dot.active {
  background-color: #3b82f6;
  transform: scale(1.3);
}

/* Özellik etiketleri */
.features-container {
  display: flex;
  gap: 12px;
  justify-content: center;
  flex-wrap: wrap;
}

.feature-tag {
  background-color: rgba(59, 130, 246, 0.2);
  color: #60a5fa;
  padding: 6px 12px;
  border-radius: 16px;
  font-size: 12px;
  border: 1px solid rgba(59, 130, 246, 0.3);
}

/* Sidebar - DÜZELTME YAPILDI! */
.sidebar {
  width: 320px;
  min-width: 320px;
  background-color: #1e293b;
  border-left: 1px solid #334155;
  padding: 32px;
  
  /* ÖNEMLİ DEĞİŞİKLİKLER: */
  height: auto !important;        /* 100vh değil, otomatik yükseklik */
  min-height: 100vh;             /* Minimum ekran yüksekliği */
  overflow-y: visible !important; /* auto değil, visible */
  overflow-x: hidden;            /* Yatay taşmayı engelle */
  
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

/* Hakkımda bölümü CSS'i de ekleyelim */
.about-section {
  padding: 1.5rem 0;
  margin: 1.5rem 0;
  border-top: 2px solid rgba(255, 255, 255, 0.1);
  border-bottom: 2px solid rgba(255, 255, 255, 0.1);
}

.about-title {
  font-size: 1.3rem;
  font-weight: 600;
  color: #ffffff;
  margin-bottom: 1rem;
  text-align: center;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
}

.about-text {
  color: rgba(255, 255, 255, 0.9);
  font-size: 0.95rem;
  line-height: 1.6;
  margin-bottom: 1rem;
  text-align: justify;
  font-weight: 300;
}

.about-text:last-child {
  margin-bottom: 0;
}

/* Hover efekti */
.about-section:hover .about-text {
  color: rgba(255, 255, 255, 1);
  transition: color 0.3s ease;
}

/* Profil fotoğrafı */
.profile-photo {
  width: 192px;
  height: 192px;
  border-radius: 50%;
  background: linear-gradient(135deg, #3b82f6, #8b5cf6);
  padding: 4px;
  margin: 0 auto 32px auto;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.profile-img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
}

.profile-placeholder {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #e2e8f0;
  display: none;
  align-items: center;
  justify-content: center;
  color: #475569;
  font-size: 18px;
  font-weight: 500;
}

/* Profil bilgileri */
.profile-info {
  margin-bottom: 32px;
}

.profile-name {
  font-size: 24px;
  font-weight: bold;
  color: white;
  margin-bottom: 8px;
  text-align: center;
}

.profile-title {
  font-size: 18px;
  font-weight: 500;
  color: #60a5fa;
  text-align: center;
}

/* İletişim linkleri */
.contact-links {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.contact-link {
  display: flex;
  align-items: center;
  color: #cbd5e1;
  text-decoration: none;
  padding: 12px;
  border-radius: 8px;
  transition: all 0.3s ease;
  gap: 12px;
}

.contact-link:hover {
  color: #60a5fa;
  background-color: rgba(51, 65, 85, 0.5);
}

.contact-icon {
  width: 20px;
  height: 20px;
}

.contact-text {
  font-size: 14px;
}

/* Body ve HTML scroll ayarları */
body, html {
  margin: 0;
  padding: 0;
  overflow-x: hidden; /* Yatay scroll yok */
  /* overflow-y: auto; - Varsayılan, değiştirmeye gerek yok */
}

/* Responsive tasarım */
@media (max-width: 1024px) {
  .app-container {
    flex-direction: column;
  }
  
  .sidebar {
    width: 100%;
    height: auto;
    min-height: auto; /* Mobilde minimum yükseklik kaldır */
    order: -1;
  }
  
  .main-content {
    padding: 16px;
  }
  
  .project-card {
    padding: 24px;
    min-height: 400px;
  }
  
  .project-title {
    font-size: 24px;
  }
  
  .prev-button {
    left: -16px;
  }
  
  .next-button {
    right: -16px;
  }
}

@media (max-width: 768px) {
  .button-container {
    flex-direction: column;
    align-items: center;
  }
  
  .link-button {
    width: 200px;
    justify-content: center;
  }
  
  .project-image {
    max-width: 250px;
    height: 250px; /* Mobil için küçültüldü */
  }
  
  .mobile-frame {
    width: 120px;
    height: 210px;
  }
  
  .mobile-screen {
    height: 160px;
    margin-top: 20px;
  }
  
  .mobile-placeholder {
    font-size: 8px;
  }
  
  .nav-button {
    width: 40px;
    height: 40px;
  }
  
  .about-section {
    padding: 1rem 0;
    margin: 1rem 0;
  }
  
  .about-title {
    font-size: 1.2rem;
  }
  
  .about-text {
    font-size: 0.9rem;
    text-align: left;
  }
}